
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
;
export default {
  components: {
    Flexbox,
    FlexboxItem
  },
  data() {
    return {
      incomes: {
        id: '',
        month: 0,
        userId: '',
        week: 0,
        history: 0,
        total: 0,
        balance: 0,
        totalAuthMoney: 0,
      }
    }
  },
  mounted(){
    document.getElementById("title").innerHTML = "我的收益";
    var userId = M.user.userId;
    !userId ? userId = 0 : userId = userId;
    var _self = this;
    _self.H.toAjax(true).then(function (data) { //data 公用参数
      var submitData = data;
      submitData.data = {
        userId: userId,
      };

      _self.A.post("/v1/incomeRecord/get", submitData).then(function (result) {
        if (result.data.state.code == 0) {
          var incomes = result.data.data.incomes;
          console.log(incomes);
          _self.as.extend(true, _self.incomes, incomes);
        } else {
          _self.$emit("modalAlert", result.data.state.msg, "确定");
        }

      });
    });
  },
  methods: {
    goToPage(urlValue){
      this.$router.push({path: urlValue, query: {history: this.incomes.total, balance: this.incomes.balance}});
    }
  }
}



